<template>
<div class="s-fixed">
    <nav :class="this.css+'-homenav-nav'">
        <div id="menu-list" class="">
            <ul :class="this.css+'-homenav-ul'">
                <li :key="key" v-for="(obj,key) in rootmenu" :class="css+'-homenav-li'+(!obj.isleaf?' dropdown':'')" @mouseenter="naventer($event,obj)" @mouseleave="navleave($event)">
                    <a :class="css+'-homenav-a'" href="#" @click="navclick(obj)" v-text="obj.name"></a>
                    <navmenu v-if="!obj.isleaf" :parentdata="obj" :rootdata="menu" :second="true" :mainiframe="mainiframe"></navmenu>
                </li>
            </ul>
        </div>
    </nav>
</div>
</template>

//script
<script>
let _TYPE={
    jump:"jump",
    iframe:"iframe",
    mainPage:"mainPage",
}
export default {
    data() {
        //                                          page                                 objList                   list（如父组件中dataList.moduleName与ref不同，此处会获取不到）
        let list = this.$store.state.dataModuleList[this.$parent.$options._componentTag][this.dataList.moduleName][this.$parent.$refs[this.dataList.moduleName].objListTag]
        // 由于v-for v-if无法同时使用，这里对数据进行过滤
        let rdata={}
        for (const key in list) {
            if(list[key].pid==list[key].id||list[key].pid==""){
                rdata[key]=list[key]
            }
        }
        //css皮肤
        let skin = this.$store.state.dataModuleList["undefined"]["skinList"]["list"]["0"]["skin"];
        return {
            rootmenu:rdata,
            menu:list,
            mainiframe:this.$parent,
            css:skin,
        };
    },
    props:['dataList'],
    methods:{
        /** 悬停监听事件 */
        naventer:($event,obj)=>{
            if(!obj.isleaf){
                $event.currentTarget.className+=" open"
            }
        }, 
        /** 悬停监听事件 */
        navleave:($event)=>{
            $event.currentTarget.className=$event.currentTarget.className.replace(" open","")
        },
        /** 点击导航栏 */
        navclick:function(obj){
            if(obj.isleaf){
                let url;
                if(obj.url.search("http")!=-1){
                    url = obj.url
                }else{
                    url = window.location.origin+window.location.pathname.substring(0, window.location.pathname.lastIndexOf("/")+1)+obj.url;
                }
                //window.console.info(url)
                if(obj.type==_TYPE.jump){
                    window.location.href=url;
                }else if(obj.type==_TYPE.iframe||obj.type==_TYPE.mainPage){
                    let mainiframe = this.mainiframe.$refs.mainiframeURL;
                    if(mainiframe.getIndex(0)==undefined){
                        mainiframe.add()
                    }
                    mainiframe.getIndex(0)
                    mainiframe.setValue("id",1)
                    mainiframe.setValue("url",url)
                    mainiframe.setValue("type",obj.type)
                }
                //标签
                if(obj.tags!=undefined){
                    let tagsList = this.mainiframe.$refs.tagsList;
                    if(tagsList.getIndex(0)==undefined){
                        tagsList.add()
                    }
                    tagsList.getIndex(0)
                    tagsList.setValue("id",1)
                    tagsList.setValue("tags",obj.tags)
                }else{
                    let tagsList = this.mainiframe.$refs.tagsList;
                    if(tagsList.getIndex(0)==undefined){
                        tagsList.add()
                    }
                    tagsList.getIndex(0)
                    tagsList.setValue("id",1)
                    tagsList.setValue("tags",undefined)
                }
            }
        }
    },
    mounted(){//初始化显示主页
        let url = window.location.origin+window.location.pathname.substring(0, window.location.pathname.lastIndexOf("/")+1)+"mainpage.html";
        let mainiframe = this.mainiframe.$refs.mainiframeURL;
        if(mainiframe.getIndex(0)==undefined){
            mainiframe.add()
        }
        mainiframe.getIndex(0)
        mainiframe.setValue("id",1)
        mainiframe.setValue("url",url)
        mainiframe.setValue("type",_TYPE.mainPage)
    }
};
</script>
<style scoped>
.default-blue-homenav-a:link,.default-blue-homenav-a:visited{
    font-weight:bold;
    color:#e7feff;
    padding:10px;
    text-decoration:none;
    text-transform:uppercase;
}
.default-blue-homenav-a:hover,.default-blue-homenav-a:active{
    background-color:rgb(21,111,199);
}
.default-blue-homenav-ul{
    list-style-type:none;
    margin:0;
    padding:0;
    padding-top:6px;
    padding-bottom:6px;
}
.default-blue-homenav-li{
    position:relative;
}
@media screen and (min-width: 768px) {
    .default-blue-homenav-a{
        text-align:center;
    }
    .default-blue-homenav-nav{
        background-image: linear-gradient(to right, rgb(8,139,215) , rgb(14,178,234));
        border: 1px solid rgb(65,167,225);
        border-left: 0px;
        border-right: 0px;
        box-shadow: 0px 0px 1px 1px rgb(65,167,225);
    }
    .default-blue-homenav-li{
        position:relative;
        display:inline;
    }
}
@media screen and (max-width: 767px) {
    .default-blue-homenav-ul{
        position: absolute;
    }
    /** 悬停显示导航菜单 
    #d-menulist:hover+.navbar,.navbar:hover{
        * 在使用css3 transtion做动画效果时，优先选择transform，尽量不要使用height，width，margin和padding，会卡顿
          * 使用transform做动画效果的运行效率比使用height，width，margin，padding作为transition的值时高 *
        transform:translate(0%,0);
    }
    */
    .default-blue-homenav-a{
        display:block;
        line-height: 1em;
        width:17em;
        text-align:left;
    }
    .s-fixed{
        transition: 0.4s;
        left:-100%;
        background-image: linear-gradient(to right, rgb(8,139,215) , rgb(14,178,234));
        overflow-x: hidden;
        position: fixed;
        top:0em;
        z-index: 100;
        height: 100%;
        width:16em;
    }
    .s-fixed::before{
        position:fixed;
        top:0.5em;
        left: 0.5em;
        content:"";
        width: 2em;
        height: 2em;
        background-color:rgb(8,139,215); 
        border-radius: 2em;
        border: 1px solid rgb(65,167,225);
        box-shadow: 0px 0px 1px 1px rgb(65,167,225);
    }
    .s-fixed:hover{
        left:0%;
    }
    .dropdown::before{
        content:">";
        position: absolute;
        top:0.5em;
        right: 2em;
        color:#e7feff;
    }
}
</style>