<template>
<div>
    <div class="tag" :key="i" :value="item" v-for="(item,i) in this.data1"><span class="tag-dot"></span>{{item}}</div>
</div>
</template>
<script>
export default {
    data(){
        return{}
    },
    props:['dataList'],
    computed:{
        data1:{
            get(){
                let dataList = this.$store.state.dataModuleList[this.$parent.$options._componentTag][this.dataList.moduleName][this.$parent.$refs[this.dataList.moduleName].objListTag][0];
                if(dataList!=undefined&&dataList.tags!=undefined&&dataList.tags!=null&&dataList.tags.trim()!=""){
                    return dataList.tags.split(",");
                }
                return []
            }
        },
    },
}
</script>

<style scoped>
.tag{
    display:inline-block;
    position: relative;
    background-color: wheat;
    font-size:xx-small;
    border-style:solid;
    border-left-style:none;
    border-radius: 2px 3px 3px 2px;
    border-width:1px;
    border-color:rgb(192, 192, 192);
    padding:1px 4px 2px 4px;
    transform: scale(0.85);
    margin-left: 1.5em;
}
.tag:before,.tag:after{
    content: "";
    position: absolute;
    line-height: 0px;/*为了防止ie下出现梯型*/
    border-style: solid;
    border-color:transparent;
    border-right-color: rgb(192, 192, 192);
}
.tag:before{
    top:-0.1em;
    left:-1.78em;
    border-width:0.93em;
    border-right-color: rgb(192, 192, 192);
}
.tag:after{
    top:-0.015em;
    left:-1.52em;
    border-width:0.83em;
    border-right-color:wheat;
}
.tag-dot{
    position: absolute;
    top:0.6em;
    left:-0.2em;
    z-index: 1;
    line-height: 0px;
    border-style:solid;
    border-radius: 30px;
    border-width:1px;
    border-color:rgb(192, 192, 192);
    padding:1px;
}
</style>