<template>
<div>
    <slot></slot>
    <!-- 单向绑定示例 -->
    <table>
        <tr><!-- 表头获取字段名 -->
            <th :key="index" :value="obj" v-for="(obj,index) in dataModulePrototype">{{obj.label}}</th>
        </tr>
        <tr :key="key" :value="obj" v-for="(obj,key) in list">
            <td :key="key1" :value="val" v-for="(val,key1) in obj">{{obj[key1]}}</td>
        </tr>
    </table>
    <!-- VUEX:双向数据绑定示例 -->
    <table>
        <!-- 可以直接绑定到state上，但不符合VUEX的要求，且无法被调试追踪到
            <input type="text" :key="key1" v-model="list1.key.key1" v-for="(val,key1,i) in obj">
        -->
        <tr :key="key" :value="obj" v-for="(obj,key,index) in list">
            <!-- VUEX:复杂对象多个循环，不可使用v-model进行computed的双向绑定，因为仅get能传其他参数，set无法传其他参数 -->
            <input type="text" :key="key1" :value="obj[key1]" v-for="(val,key1) in obj" @input="input(index,key1,$event.target.value)"> 
        </tr>
    </table>
</div>


</template>

<script>
export default {
    data(){
        return{}
    },
    props:['dataList'],
    computed:{
        list:{
            get(){
                //                                      page                                 objList                   list（如父组件中dataList.moduleName与ref不同，此处会获取不到）
                return this.$store.state.dataModuleList[this.$parent.$options._componentTag][this.dataList.moduleName][this.$parent.$refs[this.dataList.moduleName].objListTag]
            },
        },
        dataModulePrototype:{
            get(){
               return this.dataList.fields
            },
        }
    },
    methods:{
        input:function(index,key1,value){
            this.$parent.$refs[this.dataList.moduleName].getIndex(index).setValue(key1,value);
        }
    }
}
</script>