<template>
<!-- 树形递归组件 -->
<ul :class="this.css+'-navmenu-ul '+(this.second?'dropdown-menu dropdown-menu-second':'dropdown-menu')">
    <li :key="key" v-for="(obj,key) in currentdata" :class="css+'-navmenu-li '+(!obj.isleaf?'dropdown':'')" @mouseenter="naventer($event,obj)" @mouseleave="navleave($event)">
        <a :class="css+'-navmenu-a'" href="#" @click="navclick(obj)" v-cloak>&nbsp;&nbsp;{{obj.name}} </a>
        <navmenu v-if="!obj.isleaf" :parentdata="obj" :rootdata="rdata" :mainiframe="mainiframe"></navmenu>
    </li>
</ul>
</template>

<script>
let _TYPE={
            jump:"jump",
            iframe:"iframe",
            mainPage:"mainPage",
        }
export default {
    data() {
        let currentdata={}
        let parentdataid = this.parentdata.id
        for (const key in this.rootdata) {
            if(this.rootdata[key].pid==parentdataid&&this.rootdata[key].pid!=this.rootdata[key].id){
                currentdata[key]=this.rootdata[key]
            }
        }
        let rdata = this.rootdata
        //css皮肤
        let skin = this.$store.state.dataModuleList["undefined"]["skinList"]["list"]["0"]["skin"];
        return {
            parentdataid : parentdataid,
            currentdata : currentdata,
            rdata : rdata,
            css:skin,
        }
    },
    props:['parentdata','rootdata','second','mainiframe'],
    methods:{
        /** 悬停监听事件 */
        naventer:($event,obj)=>{
            if(!obj.isleaf){
                $event.currentTarget.className+=" open"
            }
        },
        /** 悬停监听事件 */
        navleave:($event)=>{
            $event.currentTarget.className=$event.currentTarget.className.replace(" open","")
        },
        /** 点击导航栏 */
        navclick:function(obj){
            if(obj.isleaf){
                let url;
                if(obj.url.search("http")!=-1){
                    url = obj.url
                }else{
                    url = window.location.origin+window.location.pathname.substring(0, window.location.pathname.lastIndexOf("/")+1)+obj.url;
                }
                //window.console.info(url)
                if(obj.type==_TYPE.jump){
                    window.location.href=url;
                }else if(obj.type==_TYPE.iframe){
                    let mainiframe = this.mainiframe.$refs.mainiframeURL;
                    if(mainiframe.getIndex(0)==undefined){
                        mainiframe.add()
                    }
                    mainiframe.getIndex(0)
                    mainiframe.setValue("id",1)
                    mainiframe.setValue("url",url)
                    mainiframe.setValue("type",obj.type)
                }
                //标签
                if(obj.tags!=undefined){
                    let tagsList = this.mainiframe.$refs.tagsList;
                    if(tagsList.getIndex(0)==undefined){
                        tagsList.add()
                    }
                    tagsList.getIndex(0)
                    tagsList.setValue("id",1)
                    tagsList.setValue("tags",obj.tags)
                }else{
                    let tagsList = this.mainiframe.$refs.tagsList;
                    if(tagsList.getIndex(0)==undefined){
                        tagsList.add()
                    }
                    tagsList.getIndex(0)
                    tagsList.setValue("id",1)
                    tagsList.setValue("tags",undefined)
                }
            }
        }
    },
}
</script>

<style scoped>
.default-blue-navmenu-a:link,.default-blue-navmenu-a:visited{
    font-weight:bold;
    color:#e7feff;
    padding:10px;
    text-decoration:none;
    text-transform:uppercase;
}
.default-blue-navmenu-a:visited,.default-blue-navmenu-a:hover,.default-blue-navmenu-a:active
.default-blue-navmenu-li:hover,.default-blue-navmenu-li:active{
    background-color:rgb(21,111,199);
}
.default-blue-navmenu-ul{
    background-image: linear-gradient(to right, rgb(8,139,215) , rgb(14,178,234));
}
.dropdown::before{
    content:">";
    position: absolute;
    right: 2em;
    color:#e7feff;
}
@media screen and (min-width: 768px) {
    .default-blue-navmenu-a{
        text-align:left;
        line-height: 0.8em;
    }
    .dropdown:hover>.dropdown-menu {
        left: 100%;/**右侧向展开 */
        top:0;
        display: block;
        margin-top: 0; 
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        border-radius: 0;
    }
    /*为实现第2层下拉，从第3层菜单才开始侧向展开效果*/
    .dropdown:hover>.dropdown-menu-second{
        left: 0;
        top:1.8em;
    }
    .default-blue-navmenu-ul{
        position:absolute;
        top:1.8em;
        left: 0;
        z-index: 1000;
        display: none;
        float: left;
        min-width: 14em;
        padding: 5px 0;
        margin: 2px 0 0;
        font-size: 14px;
        text-align: left;
        list-style: none;
        background-clip: padding-box;
        border: 1px solid #ccc;
        border: 1px solid rgba(0,0,0,.15);
        border-radius: 4px;
        -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
        box-shadow: 0 6px 12px rgba(0,0,0,.175);
    }
    .default-blue-navmenu-li{
        position:relative;
        margin:0;
        padding:0;
        padding-top:0em;
        padding-bottom:0em;
    }
    .dropdown::before{
        top:0.5em;
    }
}
@media screen and (max-width: 767px) {
    .default-blue-navmenu-ul{
        position:relative;
        display: none;
        min-width: 13.5em;
        font-size: 14px;
        text-align: left;
        list-style: none;
    }
    .default-blue-navmenu-a{
        display:block;
        line-height: 0.5em;
        width:17em;
        text-align:left;
    }
    .dropdown:hover>.dropdown-menu {
        top:0;
        display: block;
        margin-top: 0; 
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        border-radius: 0;
    }
}
</style>