<template>
<div v-if="this.data1" :class="(this.data1.type!='mainPage')?this.css+'-iframe':''" style="overflow:visible">
    <iframe allowtransparency="true" ref="iframe" v-if="this.data1&&(this.data1.url!='')" @load="iframeload" :style="{height:iframeHeight,width:'100%'}" scrolling="no" frameborder="0" class="embed-responsive-item" :src="this.data1.url" seamless></iframe>
</div>
</template>

<script>
//:style="{height:iframeHeight,width:iframeWidth}"
export default {
    data() {
        //css皮肤
        let skin = this.$store.state.dataModuleList["undefined"]["skinList"]["list"]["0"]["skin"];
        return {
            iframeHeight: "80%",
            css:skin,
        };
    },
    props:['dataList'],
    computed:{
        data1:{
            get(){
                return this.$store.state.dataModuleList[this.$parent.$options._componentTag][this.dataList.moduleName][this.$parent.$refs[this.dataList.moduleName].objListTag][0]
            }
        },
    },
    methods:{
        iframeload(){
            let ifr = this.$refs.iframe;
            this.iframeHeight=(ifr.contentWindow.document.body.scrollHeight+30)+"px"
        }
    },
}
</script>

<style scoped>
.default-blue-iframe{
    border: 1px solid gray;
    border-radius: 0.5em;
    margin: 0.5em;
    padding: 0 0.3em;
    background-color: rgb(248, 248, 250)
}
</style>