<template>
<div>
</div>
</template>

<script>
export default {
    data(){
        return{
            //指针
            pointer:null,
            //最大指针
            maxpointer:null,
            //定义的实际 list 路径名
            objListTag:"list",
            //缓存子模块路径
            childrenNamespaceMap:[]
        }
    },
    props:['listId'],
    methods:{// methods 属性中定义了当前 Vue 实例所有可用方法
        // 新增一条空数据
        add(){
            //获取到对象原型
            var pro = this.$store.state[this.$options._componentTag][this.$parent.$options._componentTag][this.listId.moduleName].dataModulePrototype
            //复制出新的对象
            var pro1 = JSON.parse(JSON.stringify(pro))
            // 深度复制无法拷贝undefind和function属性，get、set方法定义需放在创建实例之前
            Object.keys(pro1.state).forEach(fieldName => {
                // 构建get方法
                pro1.getters["get"+fieldName]=(state)=>{
                    return state[fieldName];
                }
            });
            // 注册数据模块
            if(this.maxpointer==null||this.maxpointer==undefined){//如果最大指针为null,初始化两个指针
                this.maxpointer=0
                this.pointer=0
            }else {//不为空则最大指针+1,修改当前指针为最大指针新数值
                this.maxpointer++;
                this.pointer=(0+this.maxpointer);
            }
            // 向模块的实例list集合中注册具体对象实例   dataModuleList,page                               ,ObjList               ,list
            this.$store.registerModule([this.$options._componentTag,this.$parent.$options._componentTag,this.listId.moduleName,this.objListTag,this.maxpointer],pro1)
            //this.childrenNamespaceMap.push(this.$options._componentTag+"/"+this.$parent.$options._componentTag+"/"+this.listId.moduleName+"/"+this.maxpointer);
            return this.$store.state[this.$options._componentTag][this.$parent.$options._componentTag][this.listId.moduleName][this.objListTag][this.pointer]
        },
        //获取整个数据,PS:返回的是具体数据，不可操作
        getData(){
            if(this.pointer!=null&&this.pointer!=undefined){
                return this.$store.state[this.$options._componentTag][this.$parent.$options._componentTag][this.listId.moduleName][this.objListTag][this.pointer]
            }else{//指针为null，代表无数据，返回undefined
                return undefined;
            }
        },
        //获取指定数据
        getIndex(index){
            this.pointer=index;
            if(this.pointer!=null&&this.pointer!=undefined&&this.maxpointer!=null&&this.maxpointer!=undefined){
                return this;
            }else{//指针为null，代表无数据，返回undefined
                return undefined;
            }
        },
        //获取整个list
        getList(){
            //var returnList=[];
            //遍历整个
            for (let i = 0; i < this.childrenNamespaceMap.size; i++) {
                //const element = this.childrenNamespaceMap[i];
                //this.getData(i)
            }
            //this.$store.getters[this.$parent.$options._componentTag+"/"+this.$parent.$parent.$options._componentTag+"/"+this.$parent.listId.moduleName]
        },
        //获取第一条数据
        getFirst(){
            if(this.pointer!=null&&this.pointer!=undefined){
                //this.pointer=0;
                return this;
            }else{//指针为null，代表无数据，返回undefined
                return undefined;
            }
        },
        //获取最后一条数据
        getLast(){
            if(this.pointer!=null&&this.pointer!=undefined){
                this.pointer=this.maxpointer;
                return this;
            }else{//指针为null，代表无数据，返回undefined
                return undefined;
            }
        },
        //获取下一条数据
        getNext(){
            if(this.pointer!=null&&this.pointer!=undefined){
                if(this.pointer<this.maxpointer) this.pointer++
                return this;
            }else{//指针为null，代表无数据，返回undefined
                return undefined;
            }
        },
        /**================================== */
        //获取字段值
        getValue(fieldName){
            //                         dataModuleList               /  page                                 /  objList                 /  list             /  0             /get
            return this.$store.getters[this.$options._componentTag+"/"+this.$parent.$options._componentTag+"/"+this.listId.moduleName+"/"+this.objListTag+"/"+this.pointer+"/get"+fieldName]
        },
        //修改字段值
        setValue(fieldName,value){
            //window.console.info("setValue被调用",fieldName,value)
            //                 dataModuleList               /  page                                 /  objList                 /  list             /set
            this.$store.commit(this.$options._componentTag+"/"+this.$parent.$options._componentTag+"/"+this.listId.moduleName+"/"+this.objListTag+"/set"+fieldName,{pointer:this.pointer,value:value});
        },
    },
    created(){
        //data methods 等 都初始化
        var dataModulePrototype={
            namespaced: true,
            state:{},
            getters:{},
            actions:{},
            mutations:{},
            modules:{}
        };
        // 读取传入参数，构建对象原型
        // 遍历fields[]数组,
        Object.keys(this.listId.fields).forEach(index => {
            var fieldName=this.listId.fields[index].name;
            // 初始化字段
            dataModulePrototype.state[fieldName]=null;
            // 由于深度复制无法拷贝undefind和function属性，此处定义get、set方法无效，定义需放在创建实例之前
        });
        //                         dataModuleList               ,page
        var modulesNamespaceMap = [this.$options._componentTag,this.$parent.$options._componentTag];
        // 判断页面模块是否注册,未注册则先初始化注册
        if(this.$store._modulesNamespaceMap[modulesNamespaceMap[0]+"/"+modulesNamespaceMap[1]+"/"]==undefined){
             this.$store.registerModule(modulesNamespaceMap,{namespaced: true,state:{},getters:{},actions:{},mutations:{},modules:{}})
        }

        // 创建list原型的数据模型              ObjList
        modulesNamespaceMap.push(this.listId.moduleName);
        var dataset = {namespaced: true,state:{
            /* 待改进？指针或许应该定义在此？
            pointer:null,//指针
            maxpointer:null, //最大指针*/
            dataModulePrototype:dataModulePrototype,// 构建对象的原型
        },getters:{},actions:{},mutations:{},modules:{}}
        // registerModule 动态注册module
        this.$store.registerModule(modulesNamespaceMap,dataset)

        // 判断模块的实例list集合是否注册,未注册则先初始化注册
        modulesNamespaceMap.push(this.objListTag);

        //                                  dataModuleList          /  page                    /  ObjList                 /  list
        if(this.$store._modulesNamespaceMap[modulesNamespaceMap[0]+"/"+modulesNamespaceMap[1]+"/"+modulesNamespaceMap[2]+"/"+modulesNamespaceMap[3]+"/"]==undefined){
            this.$store.registerModule(modulesNamespaceMap,{namespaced: true,state:{},getters:{},actions:{},mutations:{
                ...Object.keys(dataModulePrototype.state).reduce((obj, key) => {
                    return {
                        ...obj,
                        ["set"+key]: (state, payload) => {
                            //window.console.info("set"+key+"被调用",state,payload.pointer,key,state[payload.pointer][key],payload);
                            state[payload.pointer][key]=payload.value
                        }
                    }
                }, {})
            },modules:{}})
        }
        if(this.listId.datasource){
            //根据数据源初始化数据
            for (let index = 0; index < this.listId.datasource.length; index++) {
                const obj = this.listId.datasource[index];
                this.add()
                for (const key in dataModulePrototype.state) {
                    this.setValue(key,obj[key])
                }
            }
        }
    },
}
</script>

