<template>
    <div>
        <dataModuleList :listId="dataList" ref='user'></dataModuleList>
        <testTable :dataList="dataList">
            <div>插槽</div>
        </testTable>
        <input type="button" value="增加" @click='add'>
        <input type="button" value="获得" @click='getObj'>
        <input type="button" value="修改" @click='update'>
    </div>
</template>

<script>
export default {
    data(){
        return{
            dataList:{// user 的 listId 可以定义为 user 同名
                moduleName:"user",//数据模型名，必需与ref相同，设置后不可更改
                fields:[
                    {name:"userName",label:"用户名",type:"String"},
                    {name:"userFlag",label:"用户标记",type:"Boolean"}
                ]
            },

        }
    },
    methods:{// methods 属性中定义了当前 Vue 实例所有可用方法
        add(){
            /**
             * 获得到datastore组件，得知dataset路径
             * this.$refs.user
             * 获得后默认是指向第一条数据
             * this.$refs.user.getObj
             * 调用方法
             **/
            this.$refs.user.add()
        },
        getObj(){
            window.console.info(this.$refs.user.getData(1))
        },
        update(){
            this.$refs.user.getCurrent
        }
    },
}
</script>

